<template>
  <div>
    <Modal v-model="modal1" width="360">
      <p slot="header" style="color:#f60; text-align:center">
        <Icon type="information-circled"></Icon>
        <span>错误提示：</span>
      </p>
      <div style="text-align:center">
        <p>请录入基础信息！</p>
      </div>
      <div slot="footer">
        <Button type="error" size="large" long @click="back">确定</Button>
      </div>
    </Modal>
  </div>
</template>

<script type="text/ecmascript-6">
  import { mapGetters } from 'vuex'
  import axios from 'axios'

  export default {
    data () {
      return {
        modal1: false
      }
    },
    computed: {
      ...mapGetters(['user'])
    },
    activated () {
      if (this.user.name) {
        axios.post('/risk/enter', {
          phone: this.user.phone,
          name: this.user.name,
          idNumber: this.user.identity,
          method: 'tianji.api.tianjireport.collectuser'
        }).then(response => {
          let res = response.data
          window.location.href = res.result.redirectUrl
        }).catch(err => {
          console.log(err)
        })
      } else {
        let self = this
        setTimeout(function () {
          self.modal1 = true
        }, 100)
      }
    },
    created () {
      this.$store.dispatch('setUser', {
        name: '卢彬',
        phone: '13268223344',
        identity: '440882199309080311'
      })
    },
    methods: {
      back () {
        this.modal1 = false
        this.$router.push({
          path: '/credit'
        })
      }
    }
  }
</script>




